$color1: #7EC8D9;
$color2: #5B9EA6;
$color3: #F2C166;
$color4: #BF2E21;
$color5: #D9A0A0;
$color6: #280283;


.wrapper{
  display: flex;
  flex-flow: row wrap;
  text-align: center;
  width: 100%;
  height: 100%;
  background: black;
}

.wrapper>*{
  padding: 10px;
  margin: 10px;
  flex: 1 100%;
  border-radius: 10px;
}

.wrapper *{
  font-family: 'Luckiest Guy', cursive;
}

.header{
  // background: chartreuse;
  height: 120px;
  display: flex;
}

.footer{
  background: $color2;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer a{
  text-align: center;
  padding: 0 10px 0 0;
}

.cards-sidebar{
  background: $color3;
  display: flex;
  flex-direction: column;
  // flex-wrap: wrap;
  justify-content: center;
  min-width: 240px;
}

.cards-sidebar.highlight{
  background: green;
  display: flex;
  flex-direction: column;
  // flex-wrap: wrap;
  justify-content: center;
  min-width: 240px;
  // font-size: 24px;
  // font-weight: 900;
  color: white;
}

.content{
  text-align: left;
  background: $color1;
  min-height: 200px;
  font-size: 24px;
  display: flex;
  align-items: center;
}

.main-container{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  height: 100%;
}

.main-container p{
  font-size: 30px;
  font-weight: 500;
  color: white
}

.result{
  background: $color4;
  display: flex;
  flex-direction: column;
  font-size: 24px;
}

@media all and (min-width: 800px){
  .content{
    flex: 3;
  }

  .cards-sidebar{
    order: 1;
  }

  .result{
    order: 3;
  }

  .content{
    order:2;
  }

  .footer{
    order:4;
  }

}

@media all and (min-width: 600px){
  .cards-sidebar {
    flex: 1
  }
  
  .result{
    flex: 1
  }
}

@media all and (max-width:800px){
  .cards-sidebar{
    order: 1;
  }

  .result{
    order: 3;
  }

  .content{
    order:2;
  }

  .footer{
    order:4;
  }
}

.simulation.loading{
  background: grey;
}

.side-cards > ul{
  display: flex;
  flex-wrap: wrap;
  padding: 5px;
  justify-content: center;
  align-items: center;
}

.side-cards > ul:hover{
  background: grey;
}

.side-cards > ul > img {
  // list-style-type: none;
  // background: plum;
  width: 28px;
  height: 39.2px;
  margin: 0 2px 5px 2px;
  z-index: 1;
}

.side-cards > ul > img:hover {
  cursor: pointer;
  -webkit-filter: brightness(120%);
  transform: scale(2);
  z-index: 2;
}

.range{
  display: flex;
  flex-direction: column;
}

.selected-range{
  display: flex;
}

.selected-range > ul{
  display: flex;
}

.range-options{
  display: flex;
  flex-wrap: wrap;
}

.range-selector{
  list-style-type: none;
  padding: 5px;
  margin: 5px;
  background: $color5;
  border: none;
  color: $color4;
  font-size: 20px;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  outline: 2px solid $color4;
  width: 33%;
  height: 40%
}

.range-selector:hover{
  -webkit-filter: brightness(120%);
  transform: scale(1.2);
}

.selected-range > ul > img {
  width: 60px;
  height: 84px;
  margin: 0 5px 0 0;
  z-index: 1;
  border: 2px solid $color4;
}

.selected-range > ul > img:hover {
  // background: purple;
  cursor: pointer;
  -webkit-filter: brightness(120%);
}

.range-selector.selected{
  // color: darken($color: #000000, $amount: 20%);
  background: darken($color: chocolate, $amount: 20%);
}

.range-selector:hover{
  cursor: pointer;
}

.board-cards{
  display: flex;
}

.board-cards > ul{
  display: flex;
}

.board-cards > ul > img {
  // list-style-type: none;
  // background: plum;
  width: 60px;
  height: 84px;
  margin: 0 5px 0 0;
  border: 2px solid $color4;
  z-index: 1;
}

.board-cards > ul > img:hover {
  // background: purple;
  cursor: pointer;
  -webkit-filter: brightness(120%);
}

.hole-cards{
  display: flex;
}

.hole-cards > ul{
  display: flex;
}


.hole-cards > ul > img {
  width: 60px;
  height: 84px;
  margin: 0 5px 0 0;
  border: 2px solid $color4;
  z-index: 1;
}

.hole-cards > ul > img:hover {
  // background: purple;
  cursor: pointer;
  -webkit-filter: brightness(120%);
}


#linkedin-logo{
  width: 20px;
  height: 20px;
}

#github-logo{
  width: 20px;
  height: 20px;
}

.spinner-hidden{
  width: 40px;
  height: 40px;
  display: none;
}

.spinner{
  width: 40px;
  height: 40px;
}

#main-logo{
  width: 100%;
}

.prompt{
  font-size: 24px;
  text-align: left;
  // border: 1px solid blue;
  border-radius: 10px;
  background: lightcyan;
  flex-basis: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.prompt-details{
  margin: auto;
  width: 90%;
}

.strange{
  display: flex;
}

.strange>div{
  display: flex;
  flex-direction: column;
}

.strange-img{
  flex-basis: 100px;
  font-size: 16px;
}

.strange div:last-child{
  flex-grow: 1;
}

.strange>div>img{
  width: 100%;
}

.result-display{
  font-size: 24px;
  text-align: center;
  // border: 1px solid blue;
  border-radius: 10px;
  background: lightcyan;
  flex-basis: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  outline: 5px solid $color6;
}

.result-display li {
  list-style-type: none;
}

.result>p{
  margin-bottom: 5px;
  font-size: 30px;
  font-weight: 700px;
  color: black;
}

.card-info{
  margin-top: 10px;
  font-size: 24px;
  font-weight: 700;
}

// .card-info-prompt{
//   font-size: larger;
//   font-weight: bolder;
// }

.result button{
  color:black;
  height: 33%;
  width: 75%;  
  padding: 5px;
  margin: 5px;
  border-radius: 8px;
  background: $color3;
  border: none;
  margin: auto;
  font-size: 20px;
  // width: 67%;
  font-weight: 700px;
}

.result button:active {
  background-color:$color3;
  box-shadow: 0 5px darkgoldenrod;
  transform: translateY(4px);
}

#simulation:hover{
  // color: black;
  // border: 2px solid plum;
  // background: white;
  cursor: pointer;
  -webkit-filter: brightness(120%);
  // transform: scale(1.5);
}

#reset-button{
  background: $color3;
}

#reset-button:hover{
  cursor: pointer;
  -webkit-filter: brightness(80%);
}


.about{
  position: relative;
  padding: 0 10px 0 0;
}

.about-details{
  position: absolute;
  display: none;
  border: 3px solid black;
  border-radius: 10px;
}

.about-details p{
  padding: 5px;
  font-family: 'Josefin Sans', sans-serif;
  color: black;
  font-size: 16px;
}

.about:hover{
  cursor: pointer;
}

.about:hover .about-details{
  text-align: left;
  display: block;
  width: 400px;
  background: $color5;
  z-index: 1;
  bottom:100%;
  right: 100%;
  -webkit-filter: brightness(120%);
}